import styled from "styled-components";
import Title from "../Title";
import TabBar from "./TabBar";
import Bracket from "./Bracket";

// conferences
import Sun from "../../data/conference_sun";
import Moon from "../../data/conference_moon";
import Stars from "../../data/conference_stars";
import Chaos from "../../data/conference_chaos";
import Finals from "../../data/conference_finals";

const CONFERENCE_TABS = ["Sun", "Moon", "Stars", "Chaos", "Finals"];

const Container = styled.section`
  padding: 100px 15px;
`;

class Tournament extends React.Component {
  state = {
    activeTab: 0
  };

  switchTab = newIndex => {
    console.log(`new tab ${newIndex}`);
    this.setState({
      activeTab: newIndex
    });
  };
  render() {
    return (
      <Container>
        <Title style={{ marginBottom: "0.5em" }}>Tournament Bracket</Title>
        <TabBar tabs={CONFERENCE_TABS} switchTab={this.switchTab} />
        {/* <Bracket {...Sun} /> */}
        <Bracket {...Sun} />
      </Container>
    );
  }
}

export default Tournament;
